.about{
  width: 100%;
  height: calc(100vh - 60px);
  color: black;
  background-color: #e9f5f5;

}
.primaryColor, .uppercase{
  font-size: 2rem;
  margin-top: 3.5rem;
}
.grey{
  font-size: 1.5rem;
}
.flexRow{
  display: flex;
  flex-direction: row;
  grid-gap: 10rem;

}
.container{

}
@media only screen and (max-width: 600px) {
  .flexRow{
    display: flex;
    flex-direction: column;
    grid-gap: 0.5rem;
    margin-left: 2rem;
  }
  .primaryColor, .uppercase{
    font-size: 1rem;
    margin-top: 1rem;
  }
  .textColor{
    font-size: 1.1rem;
  }
  .grey{
    font-size: 0.9rem;
  }

  .meRightImgContainer{
      margin-top: 1rem;

  }
  .meRightImgContainer img{
      width: 10rem;
      height: 100%;
      object-fit: contain;
      border-radius: 50%;
      z-index: 4;
      margin-left: 2rem;

  }
}
.meRightImgContainer {
    margin-top: 3.5rem;
    width: 15rem;
    height: 100%;
    overflow: hidden;
}
.meRightImgContainer img{
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    border-radius: 50%;
    z-index: 4;
}
.fade-right{
  margin-left: 10rem;
}
