.sliding img:nth-child(2){
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.sliding img:nth-child(3){
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.sliding img:nth-child(4){
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}
.sliding img:nth-child(5){
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
