@-webkit-keyframes slidein {
  0%{
    opacity: 0;
  }
  5%{
    opacity: 0;
    -webkit-transform: translateX(-5rem);
            transform: translateX(-5rem);
  }
  10%{
    opacity: 0;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  25%{
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  30%{
    opacity: 1;
    -webkit-transform: translateX(5rem);
            transform: translateX(5rem);
  }
  80%{
    opacity: 1;
  }
  100%{
    opacity: 1;
  }
}
@keyframes slidein {
  0%{
    opacity: 0;
  }
  5%{
    opacity: 0;
    -webkit-transform: translateX(-5rem);
            transform: translateX(-5rem);
  }
  10%{
    opacity: 0;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  25%{
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  30%{
    opacity: 1;
    -webkit-transform: translateX(5rem);
            transform: translateX(5rem);
  }
  80%{
    opacity: 1;
  }
  100%{
    opacity: 1;
  }
}

