.skills{
  width: 100%;
  height: calc(100vh - 60px);
  background-color: white;
  align-items: center;

}
.title{
  color:black;
  text-align: center;

}
.sliding{
  animation: 4s linear 1s infinite alternate slidein;
  overflow: hidden;
  color: white;
  display: block;
  justify-content: center;
  align-items: center;
  width: 50%;
  margin-left: 20rem;
}
@media only screen and (max-width: 600px) {
  .sliding{
    margin-left: 2rem;
  }
  .image{
    margin-top: 5rem;
    height: 100%;
    width: 100%;
  }
}

@media only screen and (max-width: 600px) {
  .sliding{
    margin-left: 2rem;
  }
  .image{
    margin-top: 5rem;
    height: 100%;
    width: 100%;
  }
}
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .sliding{
    margin-left: 2rem;
    width: 100%;
  }
   }

.sliding img:nth-child(2){
  animation-delay: 1s;
}
.sliding img:nth-child(3){
  animation-delay: 2s;
}
.sliding img:nth-child(4){
  animation-delay: 3s;
}
.sliding img:nth-child(5){
  animation-delay: 4s;
}
@keyframes slidein {
  0%{
    opacity: 0;
  }
  5%{
    opacity: 0;
    transform: translateX(-5rem);
  }
  10%{
    opacity: 0;
    transform: translateX(0px);
  }
  25%{
    opacity: 1;
    transform: translateX(0px);
  }
  30%{
    opacity: 1;
    transform: translateX(5rem);
  }
  80%{
    opacity: 1;
  }
  100%{
    opacity: 1;
  }
}
