.logo{
  width: 4rem;
  margin-left: 1rem
}


a.nav-link{
  font-weight: 500;
  letter-spacing: normal;
  font-family: "KoHo" sans-serif;
  font-size: 1.2rem;
  margin-right: 3rem;
  margin-left: 3rem;
  transition: all .2s ease-in-out;
  cursor: pointer;
}

a.nav-link:hover{
transform: scale(1.2);  /* make this whatever you want */
}
a.navbar-brand:hover{
  transform: scale(1.5)
}
