.header-wraper{
  position: relative;
  background: url("./back2.jpg");
  background-size: cover;
  background-position: center;
  width: 100%;
  height: calc(100vh - 60px);
}
.main-info{
  position: absolute;
  left: 0;
  right: 0;
  top:0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "KoHo" sans-serif;
  color: White;
  font-weight: 1000;
}

.typed-text{
  font-size: 2rem;
  color: #fcf403;

}
.btn-light{
  margin-top: 10px;
  font-family: "KoHo" sans-serif;
  font-size: 1.5rem;
  padding: 0.5rem 1rem 0.5rem 1rem;

}

.btn-light:hover{
  background-color: red;
  transform: scale(1.05);
}
