.projects{
  width: 100%;
  height: calc(100vh - 60px);
  background-color: #ebf0f0;
  text-align: center;
}
.img{
  width:100%;
  height: 40vh;
  overflow: hidden;
  border-radius: 12px;
  display: inline-block;
  border: 3px solid var(--gray-2)
}
.title{
  margin-top: 1rem;
}

.name{
  margin-top: 0.5rem;
  background-color: var(--deep-dark);
  padding: 1rem;
  border-radius: 12px;
  font-size: 1.5rem;
  color: white;
}
.desc{
font-size: 1rem;
margin-left: 0.1rem;
margin-right: 0.1rem;
color: white;
}
.all{
  display:flex;
  gap:3rem;
  margin-top: 2rem;
}
.item{
  background-color: #262323;
  border-radius: 12px;
  margin-left: 0.1rem;
  margin-right: 1rem;
  z-index: 4;
}
.swiper-container {
    padding-top: 8rem;
    max-width: 100%;
  }
.swiper-button-prev,
   .swiper-button-next {
     position: absolute;
     height: 50px;
     width: 50px;
     background: var(--deep-dark);
     z-index: 10;
     right: 60px;
     left: auto;
     top: 0;
     transform: translateY(50%);
     color: var(--gray-1);
     border-radius: 8px;
   }
   .swiper-button-next {
     right: 0;
   }
   .swiper-button-prev::after,
   .swiper-button-next::after {
     font-size: 2rem;
   }
